const formatRelativeLocale = {
  lastWeek: "eeee 'pase nan l' p",
  yesterday: "'y nan l' p",
  today: "'jodi a' p",
  tomorrow: "'demen nan l' p'",
  nextWeek: "eeee 'pwochen nan l' p",
  other: "P",
};

export const formatRelative = (token, _date, _baseDate, _options) =>
  formatRelativeLocale[token];
